          >&times;</button>\n\n            <transition name=\"fade\">\n                <div v-if=\"query\" class=\"absolute left-0 right-0 md:inset-auto w-full lg:w-3/4 text-left mb-4 md:mt-10\">\n                    <div class=\"flex flex-col bg-white border border-b-0 border-t-0 border-pink-400 rounded-b-lg shadow-lg mx-4 md:mx-0\">\n                        <a\n                            v-for=\"(result, index) in results\"\n                            class=\"bg-white hover:bg-pink-100 border-b border-pink-400 text-xl cursor-pointer p-4\"\n                            :class=\"{ 'rounded-b-lg' : (index === results.length - 1) }\"\n                            :href=\"result.item.link\"\n                            :title=\"result.item.title\"\n                            :key=\"result.item.link\"\n                            @mousedown.prevent\n                        >\n                            {{ result.item.title }}\n\n                            <span class=\"block font-normal text-gray-700 text-sm my-1\" v-html=\"result.item.snippet\"></span>\n                        </a>\n\n                        <div\n                            v-if=\"! results.length\"\n                            class=\"bg-white w-full hover:bg-pink-100 border-b border-pink-400 rounded-b-lg shadow cursor-pointer p-4\"\n                        >\n                            <p class=\"my-0\">No results for <strong>{{ query }}</strong></p>\n                        </div>\n                    </div>\n                </div>\n            </transition>\n        </div>\n\n        <button\n            title=\"Start searching\"\n            type=\"button\"\n            class=\"flex md:hidden bg-gray-100 hover:bg-pink-100 justify-center items-center border border-gray-500 rounded-full focus:outline-none h-10 px-3\"\n            @click.prevent=\"showInput\"\n        >\n            <img src=\"/assets/img/magnifying-glass.svg\" alt=\"search icon\" class=\"h-4 w-4 max-w-none\">\n        </button>\n    </div>\n</template>\n\n<script>\nexport default {\n    data() {\n        return {\n            fuse: null,\n            searching: false,\n            query: '',\n        };\n    },\n    computed: {\n        results() {\n            return this.query ? this.fuse.search(this.query) : [];\n        },\n    },\n    methods: {\n        showInput() {\n            this.searching = true;\n            this.$nextTick(() => {\n                this.$refs.search.focus();\n            })\n        },\n        reset() {\n            this.query = '';\n            this.searching = false;\n        },\n    },\n    created() {\n        axios('/index.json').then(response => {\n            this.fuse = new Fuse(response.data, {\n                minMatchCharLength: 6,\n                keys: ['title', 'snippet', 'categories'],\n            });\n        });\n    },\n};\n</script>\n\n<style>\ninput[name='search'] {\n    background-image: url('/assets/img/magnifying-glass.svg');\n    background-position: 0.8em;\n    background-repeat: no-repeat;\n    border-radius: 25px;\n    text-indent: 1.2em;\n}\n\ninput[name='search'].transition-border {\n    border-bottom-left-radius: 0;\n    border-bottom-right-radius: 0;\n    border-top-left-radius: .5rem;\n    border-top-right-radius: .5rem;\n}\n\n.fade-enter-active {\n    transition: opacity .5s;\n}\n\n.fade-leave-active {\n    transition: opacity 0s;\n}\n\n.fade-enter,\n.fade-leave-to {\n    opacity: 0;\n}\n</style>\n"],"sourceRoot":""}]);
